// jest.setup.ts
// @ts-nocheck

// React 및 관련 라이브러리 임포트
import React from 'react';
import * as ReactDOM from 'react-dom';
import ReactTestRenderer from 'react-test-renderer';

// React 18+ 호환성 설정 추가
// ReactDOM v18에서는 createRoot를 사용합니다
const reactDomClient = {
  createRoot: jest.fn((container) => ({
    render: jest.fn(),
    unmount: jest.fn(),
  })),
  hydrateRoot: jest.fn()
};

// 글로벌 설정
global.__DEV__ = true;
global.window = global.window || {};

// 중요: ReactCurrentDispatcher 오류 해결을 위한 설정
global.React = React;
global.ReactDOM = {
  ...ReactDOM,
  ...reactDomClient
};
global.ReactTestRenderer = ReactTestRenderer;

// React 테스트 라이브러리 mock
jest.mock('@testing-library/react-hooks', () => {
  const originalModule = jest.requireActual('@testing-library/react-hooks');
  return {
    ...originalModule,
    renderHook: (callback) => {
      const result = { current: null };
      const hookFn = () => {
        result.current = callback();
        return null;
      };
      return { result, rerender: jest.fn(), unmount: jest.fn() };
    }
  };
});

// React Native 모킹
jest.mock('react-native', () => {
  const ReactNative = jest.requireActual('react-native');
  
  return {
    ...ReactNative,
    StyleSheet: {
      create: jest.fn(styles => styles),
      flatten: jest.fn(style => style),
    },
    Platform: {
      OS: 'android',
      select: jest.fn(obj => obj.android || obj.default),
    },
    Dimensions: {
      get: jest.fn(() => ({ width: 375, height: 667 })),
      addEventListener: jest.fn(),
      removeEventListener: jest.fn(),
    },
    View: 'View',
    Text: 'Text',
    TouchableOpacity: 'TouchableOpacity',
    Image: 'Image',
    ScrollView: 'ScrollView',
    TextInput: 'TextInput',
    Alert: {
      alert: jest.fn(),
    },
    Animated: {
      Value: jest.fn(() => ({
        setValue: jest.fn(),
        interpolate: jest.fn(() => ({
          __getValue: jest.fn(() => 0),
        })),
      })),
      View: 'Animated.View',
      Text: 'Animated.Text',
      timing: jest.fn(() => ({
        start: jest.fn(cb => cb && cb({ finished: true })),
      })),
    },
    TurboModuleRegistry: {
      getEnforcing: jest.fn(() => ({
        show: jest.fn(),
        debugRemotely: jest.fn(),
      })),
    },
    NativeModules: {
      DevSettings: { reload: jest.fn() },
      StatusBarManager: { getHeight: jest.fn(cb => cb && cb(0)) },
    },
  };
});

// 네비게이션 모킹
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: () => ({
      navigate: jest.fn(),
      goBack: jest.fn(),
    }),
    useRoute: () => ({
      params: {},
    }),
    useIsFocused: () => true,
  };
});

// React Native Safe Area Context 모킹
jest.mock('react-native-safe-area-context', () => {
  return {
    SafeAreaProvider: ({ children }) => children,
    SafeAreaView: ({ children }) => children,
    useSafeAreaInsets: () => ({ top: 0, right: 0, bottom: 0, left: 0 }),
  };
});

// 경고/에러 필터링
const originalConsoleWarn = console.warn;
const originalConsoleError = console.error;

console.warn = (...args) => {
  const warningMessage = args[0] || '';
  
  const ignoredWarnings = [
    'ProgressBarAndroid has been extracted',
    'Clipboard has been extracted',
    'TurboModuleRegistry.getEnforcing',
    'DevMenu could not be found',
    'ReactCurrentDispatcher',
    'ReactCurrentOwner'
  ];
  
  if (!ignoredWarnings.some(warning => 
    typeof warningMessage === 'string' && warningMessage.includes(warning)
  )) {
    originalConsoleWarn(...args);
  }
};

console.error = (...args) => {
  const errorMessage = args[0] || '';
  
  const ignoredErrors = [
    'act(',
    'inside a test was not wrapped in act',
    'unmounted component',
    '감정 로드 오류', 
    '챌린지 데이터 로딩 오류',
    'ReactCurrentDispatcher',
    'ReactCurrentOwner'
  ];
  
  if (!ignoredErrors.some(error => 
    typeof errorMessage === 'string' && errorMessage.includes(error)
  )) {
    originalConsoleError(...args);
  }
};

// 타이머 모킹
jest.useFakeTimers();

// Vector Icons 모킹
jest.mock('react-native-vector-icons/MaterialCommunityIcons', () => 'Icon');

// AsyncStorage 모킹
jest.mock('@react-native-async-storage/async-storage', () => ({
  setItem: jest.fn(),
  getItem: jest.fn(),
  removeItem: jest.fn(),
  clear: jest.fn(),
}));

// React-hooks 모킹
jest.mock('react', () => {
  const originalReact = jest.requireActual('react');
  
  // React 18+ 호환성을 위한 커스텀 훅 처리
  return {
    ...originalReact,
    useMemo: jest.fn((fn) => fn()),
    useEffect: jest.fn(),
    useLayoutEffect: jest.fn(),
    useContext: jest.fn(() => ({})),
    useReducer: jest.fn((reducer, initialState) => [initialState, jest.fn()]),
    useCallback: jest.fn((fn) => fn),
    useState: jest.fn(initial => [initial, jest.fn()]),
    useRef: jest.fn(initial => ({ current: initial })),
    // React 18+ createContext API
    createContext: jest.fn(() => ({
      Provider: ({ children }) => children,
      Consumer: ({ children }) => children({}),
    })),
  };
});

// @testing-library 설정
import '@testing-library/jest-native/extend-expect';